<template>

<h1>전화번호부</h1>

<h2>리스트</h2>

<p>등록된 전화번호 리스트입니다.</p>

    <table border="1">
        <colgroup>
            <col style="width: 100px;">
            <col style="width: 120px;">
            <col style="width: 120px;">
            <col style="width: 190px;">
        </colgroup>
        <thead>
            <tr>
                <th>이름(name)</th>
                <th>핸드폰(hp)</th>
                <th>회사(company)</th>
                <th>관리</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(list, i) in personList" :key="i">
                <td>{{ list.name }}</td>
                <td>{{ list.hp }}</td>
                <td>{{ list.company }}</td>
                <td>
                    <button type="button">삭제하기</button>&nbsp;&nbsp;
                    <router-link to="/">[수정폼이동]</router-link>
                </td>
            </tr>
        </tbody>	
    </table>
    <br>

<router-link to="/">등록폼 이동</router-link>

</template>

<script>
import axios from 'axios';

export default {
    name : 'ListView',
    component : {

    },
    data (){
        return {
            personList : ''
        }
    },
    methods : {
        getList(){
            console.log('getList');
            axios({
                method: 'get', 
                url: `${this.$store.state.apiBaseUrl}/phonebook`,
                headers: { "Content-Type": "application/json; charset=utf-8" }, 
                responseType: 'json' 
            }).then(response => {
                if(response.data.result === 'success'){
                    this.personList = response.data.apiData;
                } else {
                    console.log(response.data.message);
                }
            }).catch(error => {
                console.log(error);
            });
        }
    },
    created (){
        this.getList();
    }

}

</script>

